module.exports = "import React from \"react\";\nimport { TabList } from \"cpstrap\";\n\nlet data = [\n  \"\",\n  \"/\",\n  \"\",\n  \"/\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\",\n  \"\"\n];\n\nconst _onClick = item => {\n  alert(item)\n};\n\nconst Example = props => {\n  return (\n    <div className=\"row\">\n      <div className=\"col-lg-12\">\n        <TabList>\n          {data.map((item, index)=>{\n            return (\n              <div className=\"item\" key={index} onClick={() => _onClick(item)}>\n                {item}\n              </div>\n            );\n          })}\n        </TabList>\n      </div>\n    </div>\n  );\n};\n\nexport default Example;\n"